* {
    margin: 0;
    padding: 0;
    font-family: 'Oswald';
}

body {
    background-color: #000;
    overflow: hidden;
}

nav {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: none;
    text-transform: uppercase !important;
}

.myName {
    margin-top: 30px;
    margin-left: 5%;
    font-size: 20px;
    color: #000;
    padding: 10px;
    background-color: #fff;
    position: relative;
}

.myName {
    margin-left: 5%;
}

.myName>div {
    width: 10px;
    height: 10px;
    bottom: 3px;
    right: 3px;
    background-color: #000;
    position: absolute;
}

.portfolioSections {
    position: absolute;
    width: 360px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 20px;
    color: #fff;
    cursor: pointer;
    margin-right: 30px;
    transform: rotate(90deg);
    right: -180px;
    top: 300px;
}

.portfolioSections>p {
    padding-left: 10px;
    padding-right: 10px;
    font-family: 'OswaldLight' !important;
}

.portfolioSections>p:hover {
    color: #000;
    background-color: #fff;
}

.sayHello {
    width: 80%;
    height: auto;
    margin-top: 80px;
    margin-left: 10%;
    position: relative;
}

.sayHello>img:last-of-type {
    width: 100%;
    height: auto;
}

.design {
    width: 20%;
    height: auto;
    top: 0;
    right: 0;
    position: absolute;
}

@media (max-width:500px) {

    .sayHello {
        width: auto;
        margin-left: 0%;
        margin-top: 100px;
        margin-right: 50px !important;
        margin-left: 5%;
    }

    .design {
        width: 70%;
        height: auto;
        top: 210px;
        right: 0;
        position: absolute;
    }
}

/* ////////////////////////// */
.aboutMeContainer {
    width: 80%;
    height: auto;
    margin-left: 10%;
    margin-top: 100px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    position: relative;
}

@media (max-width:500px) {
    .aboutMeContainer {
        width: auto;
        margin-left: 0%;
        margin-top: 100px;
        margin-right: 50px !important;
        margin-left: 5%;
    }
}

.myPhoto {
    min-width: 200px;
    min-height: 200px;
    border: 5px #fff solid;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.myPhoto>img {
    width: 250px;
}

.aboutMe {
    width: 100%;
    color: #fff;
    font-size: 40px;
    font-family: 'extraLight' !important;
    line-height: 60px;
    text-shadow: 10px 2px 10px #000;
    position: relative;
    z-index: 1;
}

@media (max-width:800px) {
    .aboutMe {
        font-size: 35px;
        line-height: 50px;

    }
}

@media (max-width:700px) {
    .aboutMe {
        font-size: 30px;
        line-height: 40px;
    }
}

@media (max-width:600px) {
    .aboutMe {
        font-size: 25px;
        line-height: 35px;

    }
}


.designOnAboutMe {
    width: 100px;
    height: 100px;
    position: absolute;
    right: 10%;
    z-index: 0;
    background-color: #fff;
}

.designOnAboutMe>div {
    width: 10px;
    height: 10px;
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: #000;
}

/* /////////////////////////////// */
.contactContainer {
    width: 80%;
    height: auto;
    margin-left: 10%;
    margin-top: 100px;
    position: relative;
}

@media (max-width:500px) {
    .contactContainer {
        width: auto;
        margin-left: 0%;
        margin-top: 100px;
        margin-right: 50px !important;
        margin-left: 5%;
    }
}

.contactTitle {
    color: #fff;
    font-size: 45px;
    font-family: 'OswaldLight' !important;
    text-transform: uppercase !important;
}

.contactMethod {
    width: 100%;
    height: 50px;
    background-color: #111;
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-radius: 5px;
}

.contactMethod>img {
    width: 40px;
    margin-left: 10px;
}

.contactMethod>p {
    color: #fff;
    font-size: 25px;
    font-family: 'OswaldLight' !important;
    text-transform: lowercase !important;
    padding-left: 10px;
    margin-top: 2px;
}

@media (max-width:500px) {
    .contactMethod>p {
        font-size: 20px;
    }
}

.mailDesign {
    width: 700px;
    height: auto;
    position: absolute;
    right: 0%;
    top: 00px;
    opacity: 0.3;
}

/* //////////////////////////// */
.myWorks {
    width: 80%;
    height: auto;
    margin-left: 10%;
    margin-top: 100px;
    position: relative;
}

@media (max-width:500px) {
    .myWorks {
        width: auto;
        margin-left: 0%;
        margin-top: 100px;
        margin-right: 50px !important;
        margin-left: 5%;
    }
}

.myWorksTitle {
    color: #fff;
    font-size: 45px;
    font-family: 'OswaldLight' !important;
    text-transform: uppercase !important;
}

iframe {
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
}